.registrationBody {
    min-height: 100vh;
    background-image: linear-gradient(white 50%, rgb(179, 179, 179));
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    >div:nth-child(1) {
        height: 500px;
        width: 55%;
        display: flex;
        flex-direction: row;
        box-shadow: 0px 0px 5px gray;
        border-radius: 10px;
        overflow: hidden;

        div:nth-child(1) {
            width: 30%;
            height: 100%;
            background-color: rgb(0, 212, 0);
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            color: #fff;

            h1 {
                text-align: center;
            }

            button {
                background-color: #fff;
                padding: 10px 50px;
                border-radius: 50px;
                color: rgb(0, 212, 0);
                font-weight: 600;
                border: none;
                cursor: pointer;
            }

            button:active {
                background-color: #eee;
            }
        }

        div:nth-child(2) {
            width: 70%;
            height: 100%;
            background-color: white;
            display: flex;
            align-items: center;
            justify-content: center;

            form {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;

                input {
                    width: 70%;
                    padding: 10px;
                    border-radius: 5px;
                    border: none;
                    outline: none;
                    background-color: rgb(240, 240, 240);
                }

                input:focus {
                    background-color: rgb(201, 201, 201);
                }

                button {
                    background-color: rgb(0, 212, 0);
                    padding: 10px 50px;
                    border-radius: 50px;
                    color: white;
                    font-weight: 600;
                    border: none;
                    cursor: pointer;
                }

                button:active {
                    background-color: rgb(0, 184, 0);
                }

                div {
                    width: 70%;
                    padding: 10px;
                    border: 1px solid red;
                    background-color: rgb(255, 183, 183);
                    color: red;
                    text-align: center;
                    margin-bottom: 10px;
                }
            }
        }
    }
}