.myProfile {
    display: flex;
    width: 80vw;
    /* border: 1px solid black; */
    margin: 0 auto;
    align-items: start;

    >div:nth-child(1) {
        flex: 3;
        margin: 32px 0;
        padding-right: 32px;

        >div:nth-child(1) {
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 3px solid gray;

            >div:nth-child(2) {
                display: flex;
                align-items: center;
                background-color: #efefef;
                padding: 8px 16px;
                border-radius: 4px;
                cursor: pointer;
                border: 1px solid #767676;

                &:hover {
                    background-color: #e6e6e6;
                }

                &:active {
                    background-color: #f5f5f5;
                }

                svg {
                    margin-left: 8px;
                    font-size: 1.2rem;
                }
            }
        }

        >div:nth-child(2) {}
    }

    >div:nth-child(2) {
        flex: 1;
        border-left: 3px solid gray;
        position: sticky;
        top: 80px;
        margin: 32px 0;

        >div {
            border-bottom: 3px solid gray;
            padding: 16px;
            margin-left: 16px;

            >div {
                display: flex;
                justify-content: space-between;
            }

            label {
                display: block;
                margin-bottom: 8px;
                font-weight: bold;
            }

            input {
                width: 100%;
            }

            textarea {
                width: 100%;
            }
        }

        >div:nth-child(3) {
            border: none;
        }

        textarea {
            min-height: 100px;
            resize: none;
            overflow: hidden;
        }
    }
}