.chat {
    width: 100%;
    height: calc(100vh - 80px);
    display: flex;
    border: 3px solid gray;
    box-sizing: border-box;

    * {
        box-sizing: border-box;
    }

    >div:nth-child(1) {
        flex: 1;
        overflow: auto;

        >label {
            padding: 16px;
            border: 3px solid gray;
            margin: 8px;
            display: block;
            display: flex;
            align-items: center;
            gap: 4px;
            position: sticky;
            top: 8px;
            background-color: #fff;

            &::after {
                content: '';
                width: 102%;
                height: 8px;
                background-color: #fff;
                position: absolute;
                bottom: -11px;
                left: 50%;
                transform: translateX(-50%);
            }

            >input {
                flex: 1;
                border: none;
                outline: none;
            }

            >svg {
                cursor: pointer;
            }
        }

        >div:nth-child(2) {
            border: 3px solid gray;
            margin: 8px;

            >div {
                padding: 16px;
                border: 3px solid gray;
                margin: 8px;
                cursor: pointer;
            }
        }

        >div:nth-child(3) {

            >div {
                padding: 16px;
                border: 3px solid gray;
                margin: 8px;
                cursor: pointer;
                display: flex;
                justify-content: space-between;

                >div:nth-child(1) {
                    max-width: 90%;
                }

                >div:nth-child(2) {
                    width: 20px;
                    height: 20px;
                    background-color: lime;
                    border-radius: 50px;
                    border: 3px solid gray;
                }
            }
        }
    }

    >div:nth-child(2) {
        width: 20px;
        height: 20px;
        background: transparent;
        border-radius: 50px;
        border: 3px solid gray;
        margin: 8px;
    }

    >div:nth-child(3) {
        flex: 3;
        border-left: 3px solid gray;
        padding: 8px;
        display: flex;
        justify-content: center;
        align-items: center;

        >p {
            font-size: 1.2rem;
            text-decoration: underline;
        }

        >div {
            width: 100%;
            height: 100%;
            border: 3px solid gray;
            overflow: auto;
            position: relative;
            display: flex;
            flex-direction: column;

            >div:nth-child(1) {
                width: 100%;
                height: 50px;
                border-bottom: 3px solid gray;
                display: flex;
                align-items: center;
                padding: 16px;
                font-weight: bold;
                position: sticky;
                top: 0px;
                background-color: #fff;
                display: flex;
                justify-content: space-between;

            }

            >div:nth-child(2) {
                overflow: auto;
                padding: 16px;
                flex: 1;
                overflow-x: hidden;
            }

            >div:nth-child(3) {
                height: 50px;
                width: 100%;
                border-top: 3px solid gray;
                position: sticky;
                bottom: 0;
                background-color: #fff;
                display: flex;
                align-items: center;
                padding: 16px;
                gap: 8px;

                >input {
                    flex: 1;
                }

                >svg {
                    font-size: 1.4rem;
                    cursor: pointer;
                }
            }
        }
    }
}

.messages {
    width: 100%;
    /* background-color: rgb(178, 178, 178); */
    display: flex;
    margin: 4px;
}

.myMessages {
    background-color: #b3ffa5;
    color: #111b21;
    border-radius: 5px;
    padding: 6px;
    margin-left: auto;
    position: relative;
    max-width: 60%;

    &::after {
        content: '';
        display: block;
        width: 20px;
        height: 10px;
        background-color: #b3ffa5;
        position: absolute;
        top: 0;
        right: -8px;
        border-bottom-right-radius: 50px;
    }
}

.othersMessages {

    color: #111b21;
    border-radius: 5px;
    padding: 6px;
    position: relative;

    background-color: #eee;
    max-width: 60%;

    &::after {
        content: '';
        display: block;
        width: 20px;
        height: 10px;
        background-color: #eee;
        position: absolute;
        top: 0;
        left: -8px;
        border-bottom-left-radius: 50px;
    }
}