.post {
    width: 80vw;
    margin: 0 auto;


    >span {
        display: flex;
        align-items: center;
        gap: 4px;
    }

    >div:nth-child(5) {
        display: flex;
        justify-content: end;
        flex-direction: column;
        gap: 16px;

        button {
            display: flex;
            align-items: center;
            gap: 4px;

            align-self: flex-start;

            svg {
                font-size: 1.2rem;
            }
        }

        >textarea {
            overflow: hidden;
            resize: none;
            min-height: 10px;
            height: 19px;
        }
    }

    >div:nth-child(6) {
        >div {
            border: 1px solid black;
            padding: 16px;
            margin: 16px 0px;

            * {
                margin: 0;
                padding: 0;
            }
        }
    }
}