.postDisplay {
    border: 3px solid gray;
    margin: 32px 0px;
    padding: 16px;
    display: block;
    color: black;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }

    >div {
        display: flex;
        justify-content: space-between;

        >p {
            font-weight: bold;
            width: 300px;
            overflow: hidden;
            text-wrap: nowrap;
            text-overflow: ellipsis;
            text-decoration: underline;
        }
        >p:nth-child(2){
            text-align: end;
        }

    }

    >p {
        width: 100%;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
    }
}