.navBar {
    height: 80px;
    background-color: #fff;
    box-shadow: 0px 0px 10px rgb(228, 228, 228);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 64px;
    position: sticky;
    top: 0px;
    z-index: 9;

    >div:nth-child(1) {
        font-size: 1.3rem;

        a {
            text-decoration: none;
        }
    }

    >div:nth-child(2) {
        display: flex;
        align-items: center;
        gap: 32px;

        >a:nth-child(1) {
            font-size: 1.5rem;
            color: black;
        }

        >div:nth-child(2) {
            display: flex;
            gap: 16px;
            color: black;

            >div:nth-child(1) {
                font-size: 1.5rem;
                cursor: pointer;
            }

            >div:nth-child(2) {
                cursor: pointer;
            }
        }
    }
}