.newPost {
    position: absolute;
    height: 100vh;
    width: 100%;
    z-index: 10;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.152);
    display: flex;
    align-items: center;
    justify-content: center;

    >div {
        width: 800px;
        height: auto;
        padding: 32px;
        border-radius: 8px;
        background-color: white;

        >label {
            display: block;
        }

        >input {
            width: 100%;
        }

        >textarea {
            width: 100%;
            resize: none;
        }

        >div {
            display: flex;

            >button {
                display: flex;
                align-items: center;
                cursor: pointer;
                margin-right: 4px;

                >svg {
                    margin-left: 4px;
                }
            }
        }
        >span{
            margin-bottom: 4px;
        }
    }
}